
* {
  box-sizing: border-box;
}

.learnmore-wrap {
  min-height: 100vh;
  background-color: black;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.learnmore-content {
  flex: 1 1 400px;
  max-width: 600px;
  padding: 20px;
}

.learnmore-title {
  font-size: 2.5rem;
  color: var(--pink-dark);
  margin: 0 0 20px;
}

.learnmore-text {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.6;
  margin-bottom: 16px;
}

.learnmore-btn {
  display: inline-block;
  padding: 12px 30px;
  background-color: white;
  color: black;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
  border-radius: 50px;
  transition: background 0.3s ease-in-out;
}

.learnmore-btn:hover {
  background-color: var(--pink-dark);
}

.learnmore-image-wrap {
  flex: 1 1 300px;
  max-width: 400px;
  padding: 20px;
  text-align: center;
}

.learnmore-image {
  max-width: 100%;
  border-radius: 20px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
}
